export const getHolder = (out: (d: any) => any) => {
    const divDrop = document.createElement('div');
    divDrop.ondrop = (e) => {
        // this.className = '';
        e.preventDefault();
        const file = e.dataTransfer.files[0];
        const reader = new FileReader();
        reader.onload = (event) => {
            // console.log(event.target);
            out(event.target)
            // divDrop.innerText = event.target.result;
        };
        // console.log(file);
        reader.readAsText(file);
        // return false;
        return
    };
    divDrop.ondragover = () => {
        // this.className = 'hover';
        return false;
    };
    divDrop.ondragend = () => {
        // this.className = '';
        return false;
    };
    return divDrop;
}

export const geojsonDownloader = (geojsonData: string) => {
    const encode = (s: string) => {
        let out: number[] = [];
        for (let i = 0; i < s.length; i++) { out.push(s.charCodeAt(i)); }
        return new Uint8Array(out);
    };

    var data = encode((JSON.stringify(geojsonData, null, 4)));
    // console.log(data)
    var blob = new Blob([data], { type: 'esri/drawingTool' });
    let url = URL.createObjectURL(blob);
    var link = document.createElement('a');
    link.setAttribute('href', url);
    link.setAttribute('download', 'scene.json');
    var event = document.createEvent('MouseEvents');
    event.initMouseEvent('click', true, true, window, 1, 0, 0, 0, 0, false, false, false, false, 0, null);
    link.dispatchEvent(event);

}