import * as turf from '@turf/turf';

export class Scene {
    public renderer = {
        type: 'FeatureCollection',
        name: 'ready-polygon-editor',
        features: [],

    }
    public id = 'scene-polygon-editor';
    public hoveredPolygonId = null;
    public source;
    public map: mapboxgl.Map;
    constructor() {
        /* */
    }
    public initScene = () => {
        this.source = {
            'type': 'geojson',
            'data': this.getDataScene(),
            'generateId': true
        }
        this.map.addSource( this.renderer.name, this.source);

        // Add a new layer to visualize the polygon.
        this.map.addLayer({
            'id': this.id,
            'type': 'fill',
            'source': this.renderer.name, // reference the data source
            'layout': {},
            'paint': {
                'fill-color': ['get', 'color'], // blue color fill
                // 'fill-color': [
                //     'case',
                //     ['boolean',['feature-state', 'clicked'], true],
                //     '#64bdbb', // if selected true, paint in blue
                //     '#888888' // else paint in gray
                // ],

                // 'fill-color': [
                //     'case',
                //     ['boolean', ['feature-state', 'clicked'], true],
                //     '#64bdbb', // if selected true, paint in blue
                //     '#888888' // else paint in gray
                // ],

                'fill-opacity': [
                    'case',
                    ['boolean', ['feature-state', 'hover'], false],
                    0.8,
                    0.4
                ]
                // 'fill-opacity': 0.5
            }
        });
        // Add a black outline around the polygon.
        this.map.addLayer({
            'id': 'outline',
            'type': 'line',
            'source': this.renderer.name,
            'layout': {},
            'paint': {
                'line-color': '#000',
                'line-width': 1
            }
        });

        this.map.on('click', this.id, (e) => {
            // new mapboxgl.Popup()
            // .setLngLat(e.lngLat)
            // .setHTML(e.features[0].properties.name)
            // .addTo(map);
            // });
            // console.log(e.features[0].properties.uuid);
        });
        // this.map.on('mouseover', 'scene-polygon-editor', (e) => {
        //     // this.map.getCanvas().style.cursor = 'grab', //'pointer';
        //     // new mapboxgl.Popup()
        //     // .setLngLat(e.lngLat)
        //     // .setHTML(e.features[0].properties.name)
        //     // .addTo(map);
        //     // });
        //     console.log(e.features[0].properties.uuid);
        // });

        // var originalColor = "#088";             
        // this.map.on('mouseenter', this.id, (e) => {
        //     console.log(e)
        //     this.map.setPaintProperty(this.id, 'fill-opacity', 1);
        // });

        // this.map.on('mouseleave', this.id, () => {
        //   this.map.setPaintProperty(this.id, 'fill-opacity', 0.5);
        // });  

        this.map.on('mousemove', this.id, (e) => {

            if (e.features.length > 0) {
                if (this.hoveredPolygonId !== null) {
                    this.map.setFeatureState(
                        { source: this.renderer.name, id: this.hoveredPolygonId },
                        { hover: false }
                    );
                }
                this.hoveredPolygonId = e.features[0].id;
                // console.log(this.hoveredPolygonId)
                // console.log(e.features[0])
                this.map.setFeatureState(
                    { source: this.renderer.name, id: this.hoveredPolygonId },
                    { hover: true }
                );
            }
        });
        this.map.on('mouseleave', this.id, () => {
            if (this.hoveredPolygonId !== null) {
                this.map.setFeatureState(
                    { source: this.renderer.name, id: this.hoveredPolygonId },
                    { hover: false }
                );
            }
            this.hoveredPolygonId = null;
        });

    }
    public removePolygon(polygonId) {
        const indexToRemove = this.renderer.features.map((feature, i) => {
            if (feature.properties.uuid === polygonId) {
                return i
            } else {
                return -1
            }
        });

        for(let i = indexToRemove.length -1 ; i > -1; --i) {
            if (indexToRemove[i] !== -1) {
                this.renderer.features.splice(indexToRemove[i], 1);
            }
        }

        this.updateRenderer();
    }
    public pushFeature = (feature) => {
        this.renderer.features.push(feature);
    }
    public updateRenderer = () => {
        // @ts-ignore
        this.map.getSource(this.renderer.name).setData(this.getDataScene())
    }
    public updateZoom = (geojsonData) => {
        const bounds = turf.bbox(geojsonData); // Assuming you have the Turf library loaded
        // Fit the map to the bounds
        // @ts-ignore
        this.map.fitBounds(bounds, { padding: 20 });
    }
    public getDataScene() {
        return JSON.parse(JSON.stringify(this.renderer));
    }
}